<template>
  <div>
    <!-- v-if="resultList.length === 0" -->
    <van-search v-model="SearchValue" placeholder="请输入搜索关键词" shape="round"/>
<div class="search_wrap" >
      <!-- 标题 -->
      <p class="hot_title">热门搜索</p>
      <!-- 热搜关键词容器 -->
      <div class="hot_name_wrap">
        <!-- 每个搜索关键词 -->
        <span v-for="(obj,index) in hotList" :key="index"
          class="hot_item" @click="btn(obj.first)"
          >{{obj.first}}</span
        >
      </div>
      </div>
      <!-- v-else -->
    <div class="search_wrap" >
      <!-- 标题 -->
      <!-- <p class="hot_title">最佳匹配</p> -->
              <SongItem
          v-for="obj in resultList"
          :key="obj.id"
          :name="obj.name"
          :author="obj.ar[0].name"
          :id="obj.id"
        ></SongItem>
  <!-- <van-cell v-for="obj in resultList" :key="obj.id" :title="obj.name"  :label="obj.ar[0].name + ' - ' + obj.name" center>
    <template #right-icon>
<van-icon name="play-circle-o" size="0.6rem"/>
      </template>
    </van-cell> -->
    </div>
    </div>
</template>

<script>
import { hotSearchAPI, SearchResultAPI } from '@/api'
import SongItem from '@/components/SongItem.vue';
export default {
  data(){
    return{
      SearchValue:"",
      hotList:[],
      resultList:[],
      timer:null
    }
  },
  async created(){
    const res=await hotSearchAPI();
    console.log(res);
    this.hotList=res.data.result.hots
  },
  methods:{
   async btn(str){
      this.SearchValue=str
       const res=await SearchResultAPI({
        type:1,
        keywords:this.SearchValue
      });
      
       console.log(res);
       
      this.resultList=res.data.result.songs
      setTimeout(() => {
       clearTimeout(this.timer) 
      });
      
    }
    
  },
  watch:{
      SearchValue(val){
      clearTimeout(this.timer);
      if(val.length===0) return this.resultList=[];

      this.timer=setTimeout(async() => {
        console.log(this.SearchValue);
        const res=await SearchResultAPI({
        type:1,
        keywords:val
      });
      console.log(res);
      this.resultList=res.data.result.songs

      }, 500); 
  }
  },
  components:{
    SongItem
  }
}
</script>

<style scoped>
/* /deep/ .van-field__control{
  font-size: 16px !important;
}  */
.search_wrap {
  padding: 0.266667rem;
}

/*热门搜索文字标题样式 */
.hot_title {
  font-size: 0.32rem;
  color: #666;
}
.hot_name_wrap {
  margin: 0.266667rem 0;
}

/* 热搜词_样式 */
.hot_item {
  display: inline-block;
  height: 0.853333rem;
  margin-right: 0.213333rem;
  margin-bottom: 0.213333rem;
  padding: 0 0.373333rem;
  font-size: 0.373333rem;
  line-height: 0.853333rem;
  color: #333;
  border-color: #d3d4da;
  border-radius: 0.853333rem;
  border: 1px solid #d3d4da;
}
.van-cell {
  border-bottom: 1px solid lightgray;
}
</style>