import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import "@/mobile/flexible" // 适配
import "@/styles/reset.css" 
import {NavBar,Search,Tabbar,TabbarItem,Row,Col,Image as VanImage,Cell, CellGroup,Icon  } from "vant"
Vue.use(NavBar)
Vue.use(Tabbar)
Vue.use(Search)
Vue.use(TabbarItem)
Vue.use(Row)
Vue.use(Col)
Vue.use(VanImage);
Vue.use(Cell);
Vue.use(Icon);
Vue.use(CellGroup);

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
